@use './abstracts/variables/colors' as *;

/* Styling untuk heading h1-h6 */
.mce-content-body h1,
.mce-content-body h2,
.mce-content-body h3,
.mce-content-body h4,
.mce-content-body h5,
.mce-content-body h6 {
    margin-top: 0;
    font-weight: 600;
    line-height: 1.2;
}

.mce-content-body h1 {
    font-size: 40px;
    margin-bottom: 16px;
}

.mce-content-body h2 {
    font-size: 32px;
    margin-bottom: 14px;
}

.mce-content-body h3 {
    font-size: 28px;
    margin-bottom: 12px;
}

.mce-content-body h4 {
    font-size: 24px;
    margin-bottom: 10px;
}

.mce-content-body h5 {
    font-size: 20px;
    margin-bottom: 8px;
}

.mce-content-body h6 {
    font-size: 18px;
    margin-bottom: 8px;
}

/* Styling untuk paragraf */
.mce-content-body p {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 16px;
    margin-top: 0;
}

/* Styling untuk strong/bold text */
.mce-content-body strong,
.mce-content-body b {
    font-weight: 700;
    font-size: inherit;
}

/* Styling untuk emphasis/italic text */
.mce-content-body em,
.mce-content-body i {
    font-style: italic;
    font-size: inherit;
}

/* Styling untuk links */
.mce-content-body a {
    font-size: inherit;
    text-decoration: underline;
    color: $primary-color;
}

.mce-content-body a:hover {
    color: $primary-hover-color;
}

/* Styling untuk lists */
.mce-content-body ul,
.mce-content-body ol {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 16px;
    margin-top: 0;
    padding-left: 32px;
}

.mce-content-body li {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 8px;
}

.mce-content-body li a {
    font-size: inherit;
}

/* Styling untuk blockquotes */
.mce-content-body blockquote {
    font-size: 18px;
    line-height: 1.6;
    margin: 24px 0;
    padding: 16px 24px;
    border-left: 4px solid #ddd;
    background-color: #f9f9f9;
    font-style: italic;
}

/* Styling untuk code elements */
.mce-content-body code {
    font-size: 14px;
    background-color: #f4f4f4;
    padding: 2px 4px;
    border-radius: 3px;
    font-family: 'Courier New', monospace;
}

.mce-content-body pre {
    font-size: 14px;
    line-height: 1.5;
    background-color: #f4f4f4;
    padding: 16px;
    border-radius: 5px;
    overflow-x: auto;
    font-family: 'Courier New', monospace;
}

/* Styling untuk tables */
.mce-content-body table {
    font-size: 14px;
    line-height: 1.4;
    border-collapse: collapse;
    margin-bottom: 16px;
}

.mce-content-body th,
.mce-content-body td {
    padding: 0.5rem;
    border: 1px solid #ddd;
    font-size: inherit;
}

.mce-content-body th {
    font-weight: 600;
    background-color: #f9f9f9;
}

/* Styling untuk horizontal rule */
.mce-content-body hr {
    margin: 32px 0;
    border: none;
    border-top: 1px solid #ddd;
}

/* Styling untuk small text */
.mce-content-body small {
    font-size: 14px;
}

/* Styling untuk mark/highlight */
.mce-content-body mark {
    font-size: inherit;
    background-color: #fff3cd;
    padding: 2px 4px;
}

/* Styling untuk del/strikethrough */
.mce-content-body del {
    font-size: inherit;
    text-decoration: line-through;
}

/* Styling untuk ins/underline */
.mce-content-body ins {
    font-size: inherit;
    text-decoration: underline;
}

/* Styling untuk sub and sup */
.mce-content-body sub,
.mce-content-body sup {
    font-size: 12px;
    line-height: 0;
}

body.mce-content-body {
    margin: 0;
    font-size: 16px; /* Base font size */
    line-height: 1.6;
    color: #333;
}